import noConten from "@/assets/zwsj.png";
import loading from "@/assets/loading.gif";
export default {
  data() {
    return {
      noConten: noConten,
      loading: loading,
      isShow: true,
      noDate: false, //这是一个判断是否加载的开关
      page: 1,
      pageSize: 10,
      dataList: [] // 列表数据
    };
  },
  filters: {
    formatDate: function(value) {
      let val = value * 1000;
      let date = new Date(val);
      let y = date.getFullYear();
      let MM = date.getMonth() + 1;
      MM = MM < 10 ? "0" + MM : MM;
      let d = date.getDate();
      d = d < 10 ? "0" + d : d;

      return y + "-" + MM + "-" + d;
    },
    getMon: function(value) {
      let val = value * 1000;
      let date = new Date(val);
      let MM = date.getMonth() + 1;
      MM = MM < 10 ? "0" + MM : MM;
      return MM;
    },
    uppercase: function(value) {
      if (!value) {
        return "";
      }
      return value.toUpperCase();
    }
  },
  mounted() {
    this.getList();
  },
  methods: {
    getList() {
      var that = this,
        formData = {
          sign: that.$store.state.user.sign,
          access_token: that.$store.state.user.token,
          offset: that.page, // 页码
          limit: 10 // 每页长度
        };
      that
        .$request({
          method: "POST",
          url: that.$baseURL + "/abacus/bonus-record",
          data: that.$qs.stringify(formData)
        })
        .then(function(response) {
        if (response.code == 0) {
            that.isShow = false;
            if (that.page == 1) {
              that.dataList = response.data;
            } else {
              that.dataList = that.dataList.concat(response.data);
            }
          } else {
            that.isShow = false;
            that.noDate=true;
          }
        })
        .catch(function(error) {
          console.log(error);
        });
    },
     // 上拉加载
    infinite(done) {
      if (this.noDate) {
        this.$refs.myscroller.finishInfinite(true); //这个方法是不让它加载了，显示“没有更多数据”，要不然会一直转圈圈
      } else {
        setTimeout(() => {
          this.page++; //下拉一次页数+1
          this.getList();
          done(); //进行下一次加载操作
        }, 1000);
      }
    },
    // 下拉刷新
    refresh() {
      console.log("refresh");
      this.page = 1; //重置页数刷新每次页数都是第一页
      this.noDate = false; //重置数据判断
      setTimeout(
        function() {
          this.getList();
          this.$refs.myscroller.finishPullToRefresh(); //刷新完毕关闭刷新的转圈圈
        }.bind(this),
        1500
      );
    }
 
  }
};



